.profileContainer {
  width: 80%;
  max-width: 1200px;
  margin: auto;
}


.profileBubble {
  font-size: 30px;
  padding: 20px 30px;
  color: white;
  border-radius: 50%;
  background-color: #EE748C;
}

.profileBubble:hover {
  opacity: 0.5;
  cursor: pointer;
}

.profileContent {
  text-align: center;
}

.userPosts hr {
  border: none;
  border-bottom: solid 1px #ddd;
}

.userPosts li {
  border-bottom: solid 1px #ddd;
  padding: 5px 0px;
}

.postFlex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}


