.navigationContainer {
  position: fixed;
  top: 0px;
  width: 100%;
  background-color: #01d8a2;
  height: calc(10vh);
}

.navigationContent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 80%;
  max-width: 1200px;
  margin: auto;
  height: calc(10vh);
  -ms-flex-align: center;
      align-items: center;
}

.navigationNotLoggedIn {
  display: -ms-flexbox;
  display: flex;
  width: 25%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  min-width: 150px;
  max-width: 220px;
}

.navigationLoggedIn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 25%;
  text-decoration: none;
  min-width: 150px;
  color: white;
  max-width: 225px;
}

.navigationUser {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  text-decoration: none;
}

.navigationLogo {
  font-size: 20px;
  font-weight: 700;
  color: white;
}

.linkStyles {
  text-decoration: none;
  color: white;
  font-weight: 700;
}

.logoutButton {
  cursor: pointer
}

.navigationPlaceholder {
  top: 0px;
  height: calc(10vh);
}

@media (max-width: 800px) {
  .navigationContent {
    width: 95%;
    max-width: none;
  }

  .navigationLoggedIn {
    width: 50%;
  }
}


