.welcomeContainer {
}

.welcomeMessage h1 {
  display: inline;
  font-size: 40px;
  color: #444;
}

.welcomeMessage h1:nth-child(2) {
  color: #fe6b8b;
}

.createPostButton {
  margin-top: 10px;
  padding: 10px 15px;
  font-size: 18px;
  background-color: #01d8a2;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  outline: none;
}

@media (max-width: 800px) {
  .welcomeMessage h1 {
    font-size: 30px;
  }

  .createPostButton {
    padding: 7px 12px;
    font-size: 14px;
  }
}


